import CommList from '@/components/common/CommList.vue';
export default {
	components: {
		CommList
	},
	data() {
		return {
			info:{
				username:"昵称",
				userpic:"/static/default.jpg",
				newstime:"2019-10-20 下午04:30",
				isFollow:false,
				title:"我是标题",
				titlepic:"/static/demo/datapic/11.jpg",
				support:{
					type:"support", // 顶
					support_count:1,
					unsupport_count:2
				},
				comment_count:2,
				share_num:2,
				content:"帝莎编程学院：uni-app第二季仿商城类实战项目开发，uni-app第二季仿微信实战项目开发",
				images:[{
					url:"https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/4.jpg"
				},{
					url:"https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/5.jpg"
				}]
			}
		}
	},
	
	computed: {
		imagesList() {
			return this.info.images.map(item=>item.url) 
		}
	},
	
	onLoad(e) {
		//初始化
		this.initData(JSON.parse(e.detail))
	},	
	methods: {
		initData(data){
			//修改标题
			uni.setNavigationBarTitle({
				title: data.title
			})
			//请求数据库
		},
		//点击评论
		doComment(){
			
		},
		
		//点击分享
		doShare(){
			
		},
		
		//关注功能
		follow(){
			this.info.isFollow = true,
			uni.showToast({
				title: '关注成功'
			})
		},
		
		//点赞功能
		doSupport(e){
			//拿到当前对象
			let item = this.info;
			// 定义信息、
			let msg = e.type === 'support' ? '点赞' : '踩';
			//用户已经操作过
			if (item.support.type === e.type) {
				return uni.showToast({
					title: '你已经操作过了',
					icon: 'none'
				});
			}//用户还没进行过操作
			else if (item.support === '') {
				item.support[e.type + '_count']++;
			} else if (item.support.type === 'unsupport' && e.type === 'support') {
				// 点赞操作，点赞数加一，取消赞数-1
				item.support.support_count++;
				item.support.unsupport_count--;
			} else if (item.support.type === 'support' && e.type === 'unsupport') {
				//反之亦然
				item.support.support_count--;
				item.support.unsupport_count++;
			}
			//保存用户点击的支持类型
			item.support.type = e.type;
			uni.showToast({
				title: msg + '成功'
			});
		},
		
		// 预览图片
		preview(index){
			// 预览图片
			uni.previewImage({
				current:index,
				urls:this.imagesList
			});
		},
		
		
		
	}
}
